<template>
  <div class="note-sidebar">
    <span class="btn add-note" @click="onAddNoteItem"></span>

    <el-dropdown
      class="notebook-title"
      placement="bottom"
      @command="handleCommand"
    >
      <span class="el-dropdown-link">
        {{ curNote.title }}<i class="iconfont icon-down"></i>
      </span>

      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item
          v-for="note in noteList"
          :key="note.id"
          :command="note.id"
          >{{ note.title }}</el-dropdown-item
        >
        <el-dropdown-item command="trash"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>

    <div class="menu">
      <div></div>
      <div></div>
    </div>

    <ul class="notes">
      <li v-for="noteItem in noteItems">
        <router-link
          :to="`/note?noteId=${curNote.id}&noteItemId=${noteItem.id}`"
        >
          <span class="date">{{ noteItem.friendlyUpdatedAt }}</span>
          <span class="title">{{ noteItem.title }}</span>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
// import notes from "../apis/list";
// import noteItems from "../apis/noteItem";
// import Bus from "../helpers/bus";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";

export default {
  created() {
    //
    this.getNoteList()
      .then(() => {
        this.setCurNote({ curNoteId: this.$route.query.noteId }); //$route.query.noteIdid
        if (this.curNote.id)
          return this.getNoteItems({ noteId: this.curNote.id });
      })
      .then(() => {
        this.setCurNoteItem({ curNoteItemId: this.$route.query.noteItemId });
        //this.$route.query.noteItemIdundefindcurNoteItemIdundefined
        this.$router.replace({
          path: "/note",
          query: {
            noteId: this.curNote.id,
            noteItemId: this.curNoteItem.id
          }
        });
      });
  },

  data() {
    return {};
  },

  computed: {
    ...mapGetters(["noteList", "noteItems", "curNote", "curNoteItem"])
  },

  methods: {
    ...mapMutations(["setCurNote", "setCurNoteItem"]),

    ...mapActions(["getNoteItems", "addNoteItem", "getNoteList"]),

    handleCommand(noteId) {
      if (noteId == "trash") {
        return this.$router.push({ path: "/trash" });
      }
      this.$store.commit("setCurNote", { curNoteId: noteId });

      this.getNoteItems({ noteId }).then(() => {
        this.setCurNoteItem(); //payloadcurNoteItem = noteItems[0] || {}
        this.$router.replace({
          path: `/note/?noteId=${this.curNote.id}&noteItemId=${this.curNoteItem.id}`
        }); //
      });
    },

    onAddNoteItem() {
      this.addNoteItem({ noteId: this.curNote.id });
    }
  }
};
</script>

<style lang="less">
@import url(../assets/css/note-sidebar.less);
</style>
