<template>
  <div id="note" class="detail">
    <NoteSidebar @update:noteItems="val => (noteItems = val)"></NoteSidebar>
    <div class="note-detail">
      <div class="note-empty" v-show="!curNoteItem.id"></div>
      <div class="note-detail-ct" v-show="curNoteItem.id">
        <div class="note-bar">
          <span> : {{ curNoteItem.friendlyCreatedAt }}</span>
          <span> : {{ curNoteItem.friendlyUpdatedAt }}</span>
          <span>{{ editStatus }}</span>
          <span class="iconfont icon-delete" @click="onDeleteNoteItem"></span>
          <span
            class="iconfont icon-fullscreen"
            @click="isPreview = !isPreview"
          ></span>
        </div>

        <div class="note-title">
          <input
            id="1"
            type="text"
            :value="curNoteItem.title"
            placeholder=""
            @input="editSave"
            @keydown="editStatus = '...'"
          />
        </div>

        <div class="editor">
          <textarea
            id="2"
            v-show="!isPreview"
            :value="curNoteItem.content"
            placeholder=",  markdown "
            @input="editSave"
            @keydown="editStatus = '...'"
          ></textarea>
          <div
            class="preview markdown-body"
            v-html="previewContent"
            v-show="isPreview"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NoteSidebar from "./NoteSidebar";
import _ from "lodash";
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import markdownit from "markdown-it";
// import Auth from "../apis/auth";

let md = new markdownit();

export default {
  components: {
    NoteSidebar
  },

  created() {
    // Auth.getInfo().then(res => {
    //   if (!res.isLogin) {
    //     this.$router.push({ path: "/login" });
    //   }
    // });
    this.checkLogin();
  }, //

  mounted() {
    this.title = document.getElementById("1");
    this.textarea = document.getElementById("2");
  },

  data() {
    return {
      editStatus: "",
      isPreview: false,
      title: "",
      textarea: ""
    };
  },

  computed: {
    previewContent() {
      return md.render(this.curNoteItem.content || "");
    },

    ...mapGetters(["noteItems", "curNoteItem", "curNote"])
  },

  methods: {
    ...mapMutations(["setCurNoteItem"]),

    ...mapActions(["editNoteItem", "deleteNoteItem", "checkLogin"]),

    //lodash
    editSave: _.debounce(function() {
      if (!this.curNoteItem.id) return;
      this.editNoteItem({
        noteItemId: this.curNoteItem.id,
        title: this.title.value,
        content: this.textarea.value
      })
        .then(res => (this.editStatus = ""))
        .catch(res => (this.editStatus = ""));
    }, 300),

    onDeleteNoteItem() {
      this.deleteNoteItem({ noteItemId: this.curNoteItem.id }).then(res => {
        this.$router.replace({
          path: `/note?noteId=${this.$route.query.noteId}`
        }); //
      });
    }
  },

  beforeRouteUpdate(to, from, next) {
    this.setCurNoteItem({ curNoteItemId: to.query.noteItemId });
    next();
  } //
};
</script>

<style lang="less">
@import url(../assets/css/note-detail.less);

#note {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;
}
</style>
